body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  user-select: none;
}

.wrapper {
  display: block;
  margin: auto;
  background: linear-gradient(to bottom, rgb(30, 79, 146), rgb(0, 0, 0));
  height: 100vh;
}

.price {
  font-size: 5vmin;
  font-weight: 600;
  display: block;
  margin: auto;
  padding-top: 15vmin;
  padding-bottom: 3vmin;
  text-align: center;
  width: 100%;
  color: rgb(255, 255, 255);
  background: linear-gradient(to left, rgb(0, 43, 107), rgb(53, 110, 185));
  box-shadow: 0px 0px 30px black;
  text-shadow: 0px 0px 20px rgb(0, 0, 0);
}

.percent {
  display: inline;
  font-weight: 400;
}

.account-container {
  display: block;
  margin: auto;
  margin-top: 5vmin;
  margin-bottom: 5vmin;
  background: linear-gradient(
    to bottom right,
    rgb(50, 122, 216),
    rgb(91, 4, 126)
  );
  color: white;
  padding: 2vmin;
  border-radius: 3vmin;
  width: 14%;
  box-shadow: 0px 0px 5px black;
}

.account {
  font-size: 2vmin;
  font-weight: 600;
  display: block;
  margin: auto;
  text-align: center;
}

.button-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  margin-top: 5vmin;
}

.button-container-inner {
    width: 40%;
  display: flex;
  justify-content: space-between;
  margin: auto;
}


.icon {
  position: relative;
  height: 6vmin;
  margin-right: 2vmin;
  margin-bottom: 1vmin;
}

.icon-wallet {
  display: block;
  position: relative;
  height: 6vmin;
  margin: auto;
  margin-bottom: 2vmin;
}

@media only screen and (max-width: 800px) {
  .price {
    width: 90%;
  }

  .account-container {
    width: 50%;
  }

  .account {
    font-size: 4vmin;
  }

  .button-container {
    flex-direction: column;
  }

  .button-container-inner{
      width: 98%;
  }

  .button {
    width: 100% !important;
  }
}
